(function() {
  'use strict';

  angular
    .module('projects', ['ngAnimate', 'ngSanitize', 'ngAria', 'ui.router', 'ngFileUpload']);

})();

(function() {
  'use strict';

  angular
    .module('projects')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;



  }
})();

(function() {
  'use strict';

  angular
    .module('projects')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope) {
	  $rootScope.$on('$stateChangeError', function (event, toState, toParams, fromState, fromParams, error) {
		  $log.error('An error occurred while changing stated: ', error);

		  $log.debug('event', event);
		  $log.debug('toState', toState);
		  $log.debug('toParams', toParams);
		  $log.debug('fromState', fromState);
		  $log.debug('fromParams', fromParams);

	  });
  }

})();

(function() {
  'use strict';

  angular
    .module('projects')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('projects')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('projects')
    .config(config);

  /** @ngInject */
  function config($logProvider, $locationProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

	 $locationProvider.html5Mode(true);
  }

})();

angular.module("projects").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"mdl-layout mdl-js-layout fileUpload-layout-transparent\"><div class=\"uploadBlock\"><div ngf-drop=\"uploadFiles($files)\" class=\"drop-box\" ngf-drag-over-class=\"\'dragover\'\" ngf-multiple=\"true\" ngf-pattern=\"\'image/*,application/pdf\'\">Drop Images or PDFs files here</div><div ngf-no-file-drop=\"\">File Drag/Drop is not supported for this browser</div></div></div>");}]);